      margin-inline: calc(var(--spacing) * 0);
    }
  }
  .lg\:flex {
    @media (width >= 64rem) {
      display: flex;
    }
  }
  .lg\:w-auto {
    @media (width >= 64rem) {
      width: auto;
    }
  }
  .lg\:grid-cols-2 {
    @media (width >= 64rem) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  .lg\:grid-cols-12 {
    @media (width >= 64rem) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  .lg\:gap-24 {
    @media (width >= 64rem) {
      gap: calc(var(--spacing) * 24);
    }
  }
  .lg\:pt-0 {
    @media (width >= 64rem) {
      padding-top: calc(var(--spacing) * 0);
    }
  }
  .lg\:text-left {
    @media (width >= 64rem) {
      text-align: left;
    }
  }
}
:root {
  --bg-navy: #0A1628;
  --bg-navy-light: #112240;
  --text-primary: #FFFFFF;
  --text-secondary: #94A3B8;
  --text-accent: #38BDF8;
  --accent-blue: #3B82F6;
  --accent-purple: #8B5CF6;
  --accent-cyan: #06B6D4;
  --accent-emerald: #10B981;
  --grad-hero: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  --grad-glow: linear-gradient(135deg, rgba(59, 130, 246, 0.5), rgba(139, 92, 246, 0.5));
  --grid-color: rgba(255, 255, 255, 0.03);
}
@layer base {
  body {
    background-color: var(--bg-navy);
    background-image: radial-gradient(circle at 15% 50%, rgba(59, 130, 246, 0.08), transparent 25%),
            radial-gradient(circle at 85% 30%, rgba(139, 92, 246, 0.08), transparent 25%);
    color: var(--text-primary);
    font-family: 'Inter', sans-serif;
    overflow-x: hidden;
  }
  h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
    font-family: 'Manrope', sans-serif;
    letter-spacing: -0.02em;
  }
  body::before {
    content: "";
    position: fixed;
    inset: 0;
    z-index: -1;
    background-image: linear-gradient(to right, var(--grid-color) 1px, transparent 1px),
            linear-gradient(to bottom, var(--grid-color) 1px, transparent 1px);
    background-size: 60px 60px;
    pointer-events: none;
  }
}
@layer utilities {
  .font-display {
    font-family: 'Manrope', sans-serif;
  }
  .font-tech {
    font-family: 'Outfit', sans-serif;
  }
  .glass-card {
    background: rgba(255, 255, 255, 0.03);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border: 1px solid rgba(255, 255, 255, 0.08);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  .glass-card:hover {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.15);
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  }
  .text-gradient {
    background: linear-gradient(135deg, #60A5FA 0%, #A78BFA 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .text-gradient-cyan {
    background: linear-gradient(135deg, #22D3EE 0%, #818CF8 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .animate-float {
    animation: float 6s ease-in-out infinite;
  }
  .animate-float-delayed {
    animation: float 6s ease-in-out 3s infinite;
  }
  .animate-pulse-glow {
    animation: pulse-glow 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes pulse-glow {
  0%,
    100% {
    opacity: 1;
    box-shadow: 0 0 5px var(--accent-blue);
  }
  50% {
    opacity: .7;
    box-shadow: 0 0 20px var(--accent-purple);
  }
}
